.friendAvatar img {
    object-fit: cover;
    border-radius: 100%;
    display: inline-block;
    margin-top: -40px;
    position: absolute;
    vertical-align: middle;
    z-index: 555;

}

.friendAvatar {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    border: 4px solid #fff;
    position: relative;
    top: 40px;
    overflow: hidden;
}