
.postCard {
    background: #fff none repeat scroll 0 0;
    border: 1px solid #ede9e9;
    border-radius: 5px;
    display: inline-block;
    /*width: 100%;*/
    margin-bottom: 20px;
    padding: 20px;
    position: relative;
    width: 470px;

}
.cardTop{
    position: absolute;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
}
.friendInfo{
    position: relative;
    width: 100%;
    text-align: left;
    left: 13px;
    color: #757a95;
    font-size: 12px;
    line-height: 0.7em;
}
.avatarImg {
    width: 48px;
    height: 48px;
    object-fit: cover;
    border-radius: 100%;
    /*display: block;*/
    /*margin-top: -40px;*/
    position: relative;
    /*vertical-align: middle;*/
    z-index: 555;
    border: 3px solid white;
    top: 0px;
    left: 5px;
}
.friendInfo .published span{
    font-size: 12px;
    color: #757a95;
    position: relative;
    text-align: left;
    /*left: 30px;*/
    top: 0;
}
.friendName a{
    text-decoration: none;
    background-color: transparent;
}
.friendName{
    font-size: 15px;
    font-weight: 600;
    color: #535165;
    display: inline-block;
    position: relative;
    width: 100%;
    /*left: 48px;*/
}
.friendName:hover{
    color: #f5604f;
}

