.published{
    position: relative;
    /*left: 20px;*/

}
.published span{

}
.postMessage {
    font-size: 13px;
    font-weight: 500;
    color: #757a95;
    text-align: left;
    left: 13px;
    position: relative;
}
.posts{
    background-color: #EDF2F6;
    width: 70%;

}
.cardBottom{
    position: relative;
    top: 40px;
}
.postImg{
    width: 450px;
}
button {
    border-radius: 100%;
    /*background-color: #7750F8;*/
    border: none;
}
.likeBtn:hover{
    transition: all 0.2s;
    border: 1px solid #515365;
}
.likeBtn {
    padding: 5px;
    width: 30px;
    border-radius: 100%;
    background-color: #23d2e2;
}
.dislikeBtn{
    padding: 5px;
    width: 30px;
    border-radius: 100%;
    background-color: #7750F8;
    border: none;
}
.dislikeBtn:hover{
    transition: all 0.2s;
    border: 1px solid #515365;
}
